import React from 'react';
import styles from './CircleSelector.module.css';

function CircleSelector(props) {
    return(
        <div className={styles.CircleSelector}>
            <button onClick={() => props.handleSelect(1)} className={props.circle === 1 ? styles.selected : ''}>SELECT CIRCLE 1</button>
            <button onClick={() => props.handleSelect(2)} className={props.circle === 2 ? styles.selected : ''}>SELECT CIRCLE 2</button>
            <button onClick={() => props.handleSelect(3)} className={props.circle === 3 ? styles.selected : ''}>SELECT CIRCLE 3</button>
            <button onClick={() => props.handleSelect(4)} className={props.circle === 4 ? styles.selected : ''}>SELECT CIRCLE 4</button>
        </div>
    );
}

export default CircleSelector;