import React from 'react';
import styles from './Circles.module.css';

function Circles(props) {
    return (
        <div class={styles.Circles}>
            <div className={props.circle === 1 ? styles.selected : ''}>1</div>
            <div className={props.circle === 2 ? styles.selected : ''}>2</div>
            <div className={props.circle === 3 ? styles.selected : ''}>3</div>
            <div className={props.circle === 4 ? styles.selected : ''}>4</div>
        </div>
    );
}

export default Circles;